import React, {useContext} from 'react';
import Module from "./Module"
import {CourseContext} from "../Context/CourseContext";
import CardOverview from "../Atomic/CardOverview";

const Course = props => {
    const loadCourse = course => {
        let groups = [];

        for (let key in course.children) {
            let temp = [];
            let modules = course.children[key].children;
            for (let moduleKey in modules) {
                temp.push(<Module title={moduleKey}
                                  key={moduleKey}
                                  src={modules[moduleKey].pictureUrl}
                                  active={modules[moduleKey].active}
                />)
            }
            groups.push(
                <CardOverview title={key} key={key}>
                    <div style={{display: 'flex', paddingBottom: 16, flexWrap: 'wrap', flexDirection: 'row', alignItems: 'center', justifyContent: 'space-evenly'}}>
                        {temp}
                    </div>
                </CardOverview>
            )
        }

        return groups;
    };

    const courses = useContext(CourseContext);

    return (loadCourse(courses[props.course]))
};

export default Course;