import React from 'react';
import styled from 'styled-components';

const Base = styled.div`
    flex-grow: 1;
    margin-left: 16px;
    color: white;
`;

const FirstLine = styled.div`
    font-size: 12pt;
`;

const SecondLine = styled.div`
    color: #bbb;
    font-size: 8pt;
`;

const DoubleLineItem = props => {
    return (
        <Base>
            <FirstLine>
                {props.children}
            </FirstLine>
            <SecondLine>
                {props.subtitle}
            </SecondLine>
        </Base>
    )
};

export default DoubleLineItem;