import React from 'react';
import styled, {withTheme} from 'styled-components';
import FAB from "./FAB";
import {animated, config, useSpring} from "react-spring";
import {useClickAway} from "./Hooks";
import {useHistory} from 'react-router-dom';

const Base = styled.div`
    display: inline-block;
    user-select: none;
    position: fixed;
    bottom: 0;
    right: 0;
    z-index: 900;
    padding: 16px;
`;

const Group = animated(styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 8px;
    
    > div {
        margin-bottom: 8px;
    }
`);

const BottomBar = props => {
    const [open, setOpen] = React.useState(false);

    const openStyle = useSpring({
        transform: open ? 'translateY(0)' : 'translateY(200%)',
        config: config.gentle
    });

    const ref = useClickAway(() => setOpen(false));

    const history = useHistory();

    const close = () => {
        window.setTimeout(() => setOpen(false), 500);
    }

    const goHome = () => {
        history.push('/', 'Startseite');
        close();
    };

    const goFullScreen = () => {
        const elem = document.documentElement;

        if (elem.requestFullscreen) {
            elem.requestFullscreen();
        } else if (elem.mozRequestFullScreen) { /* Firefox */
            elem.mozRequestFullScreen();
        } else if (elem.webkitRequestFullscreen) { /* Chrome, Safari and Opera */
            elem.webkitRequestFullscreen();
        } else if (elem.msRequestFullscreen) { /* IE/Edge */
            elem.msRequestFullscreen();
        }

        close();
    };

    return (
        <Base ref={ref}>
            <Group style={openStyle}>
                <FAB size={'medium'} color={{main: props.theme.primary.main, highlight: props.theme.primary.light, color: props.theme.primary.color}} icon={'home'} onClick={goHome}/>
                <FAB size={'medium'} color={{main: '#2e7d32', highlight: '#60ad5e', color: '#ffffff'}} icon={'fullscreen'} onClick={goFullScreen}/>
                <FAB size={'medium'} color={{main: '#d84315', highlight: '#ff7543', color: '#000000'}} icon={'local_library'}/>
                <FAB size={'medium'} color={{main: '#6a1b9a', highlight: '#9c4dcc', color: '#ffffff'}} icon={'note'}/>
            </Group>
            <FAB icon={'add'}
                 size={'large'}
                 animation={{from: 'rotateZ(0deg)', to: 'rotateZ(405deg)'}}
                 active={open}
                 onClick={() => {
                     setOpen(!open);
                 }}
            />
        </Base>
    )
};

export default withTheme(BottomBar);