import React from 'react';
import styled from 'styled-components';

const Base = styled.div`
    display: flex;
    flex-direction: column;
`;

const TextField = styled.input`
    border: none;
    outline: none;
    padding: 8px;
    overflow: show;
    
    ${props => props.required ? `
    :valid + div {
        background-color: green;
    }
    
    :invalid + div:before {
        background-color: red;
    }
    
    :invalid + div {
        background-color: red;
    }
    
    :valid + div:before {
        background-color: green;
    } 
    ` : ''}
    
    :placeholder-shown + div:before {
        transform: scale(0, 1);
    }
    
    :not(:placeholder-shown) + div:before {
        transform: scale(1, 1);
    }
    
    :focus + div:before {
        transform: scale(1, 1);
    }
`;

const Line = styled.div`
    transition: background-color 0.3s ease;
    height: 1px;
    background-color: #ccc;
    position: relative;
    
    :before {
        transition: 0.3s ease transform;
        content: '';
        position: absolute;
        background-color: blue;
        width: 100%;
        transform: scale(0, 1);
        height: 1px;
    }
`;

const Input = ({value, placeholder, required, onChange, type, ...rest}) => {
    return (
        <Base>
            <TextField type={type} onChange={onChange} value={value} placeholder={placeholder} required={required} />
            <Line />
        </Base>
    )
};

export default Input;