import React from 'react';
import styled from 'styled-components';
import Modal from './Modal';
import {useRipple} from "../Hooks";

const Base = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 120px;
    text-align: center;
    padding: 8px;
    filter: grayscale(${props => props.active ? 0 : 100}%);
    position: relative;
    overflow: hidden;
`;

const Picture = styled.div`
    width: 48px;
    height: 48px;
    background-color: ${props => props.theme.secondary.main};
    border-radius: 50%;
    ${props => props.theme.elevation[2]}
    background-image: url(${props => props.src});
    background-size: cover;
    background-position: center center;
    background-color: white;
    border: 4px solid ${props => props.theme.secondary.main};
`;

const Title = styled.div`
    padding-top: 6px;
    color: ${props => props.isHeader ? props.isHeader : props.active ? 'black' : '#666'};
`;

const Module = props => {
    const [ref, ripples] = useRipple({duration: 500, color: 'rgba(0, 0, 0, 0.5)', size: 10});

    const [active, setActive] = React.useState(false);

    return (
        <>
            <Modal title={props.title}
                   active={active}
                   close={() => setActive(false)}/>
            <Base onClick={() => setActive(true)} active={props.active} ref={ref}>
                <Picture src={props.src} isHeader={props.isHeader} />
                <Title active={props.active} isHeader={props.isHeader}>
                    {props.title}
                </Title>
                {ripples}
            </Base>
        </>
    )
};

export default Module;