import React from 'react';
import Course from "../Course";
import {Route, Switch, useLocation} from "react-router-dom";
import Start from "./Start";
import Profile from "./Profile";
import Exercise from "./Exercise";
import Fader from "../Atomic/Fader";

const MainView = props => {
    const location = useLocation();

    const [state, setState] = React.useState(false);

    const render = () => {
        return (
            <Switch location={location}>
                <Route path={'/course'}>
                    <Course course={location.state}/>
                </Route>
                <Route exact path={'/'}>
                    <Start/>
                </Route>
                <Route key={'profile'} exact path={'/profile'}>
                    <Profile />
                </Route>
                <Route key={'exercise'} exact path={'/exercise'}>
                    <Exercise/>
                </Route>
                <Route key={'fader'} exact path={'/fader'}>
                    <div style={{padding: 16}} onClick={() => setState(!state)}>
                        <Fader>
                            {state ? <div key={1}>This is a test...</div> : <div key={2}>This is another test...</div>}
                        </Fader>
                    </div>
                </Route>
            </Switch>
        )
    }

    return render();
};

export default MainView;