import React from 'react';
import styled from 'styled-components';
import Input from "../Atomic/Input";
import Button from "../Atomic/Button";

const Base = styled.div`
    ${props => props.theme.elevation[3]}
    margin: 16px;
`;

const Title = styled.div`
    padding: 16px;
`;

const Content = styled.div`
    padding: 16px;
`;

const Proposition = styled.div`
    padding: 16px;
`;

const Solution = styled.div`
    padding: 16px;
`;

const Send = styled.div`
    display: flex;
    justify-content: center;
    padding-bottom: 16px;
`;

const Exercise = ({...props}) => {
    return (
        <Base>
            <Title>Aufgabe 5.2</Title>
            <Proposition>
                Berechnen Sie die Ströme I1, I2, I3 bei einem Widerstand von R=200 Ohm.
            </Proposition>
            <Solution>
                <Input placeholder={'I1'} />
                <Input placeholder={'I2'} />
                <Input placeholder={'I3'} />
            </Solution>
            <Send>
                <Button>
                    Lösung abschicken
                </Button>
            </Send>
        </Base>
    )
}

export default Exercise;