import styled from 'styled-components';
import {useRipple} from "../../Hooks";
import React from "react";

const Base = styled.li`
    position: relative;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: space-between;

    transition: 0.3s ease background-color;
    padding: 16px;
    
    :hover {
        background-color: #ccc;
    }
    
    border-top: 1px solid #ccc;
`;

const ListItem = ({children, style, onClick}) => {
    const [ref, ripples] = useRipple({duration: 2000, color: 'rgba(0, 0, 0, 0.5)', size: 50});

    return (
        <Base ref={ref} style={style} onClick={onClick}>
            {ripples}
            {children}
        </Base>
    )
}

export default ListItem;