import React from 'react';
import styled from 'styled-components';
import {CourseContext} from "./Context/CourseContext";
import {Link} from 'react-router-dom';

const Carousel = styled.div`
    width: 100%;
    display: flex;
    overflow: auto;
    padding: 16px;
    box-sizing: border-box;
`;

const Title = styled.div`
    padding: 16px;
    padding-bottom: 0;
    color: #666;
    font-size: 8pt;
`;

const Picture = styled.div`
    background: url(${props => props.pictureUrl}) center center / cover;
    height: 64px;
    width: 64px;
    overflow: hidden;
    border-radius: 50%;
    border: 2px solid white;
`;

const Item = styled(Link)`
    -webkit-tap-highlight-color: transparent;
    outline: none !important;
    flex: 1 1 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-shrink: 0;
    text-decoration: none;
    min-width: 120px;
`;

const Progress = styled.div`
    border-radius: 50%;
    padding: 2px;
    border: 2px solid ${props => props.theme.secondary.main};
    ${props => props.theme.elevation[3]}
`;

const CourseOverview = props => {
    const courses = React.useContext(CourseContext);

    const constructCourseCards = (courses) => {
        let courseCards = [];
        for (let key in courses) {
            courseCards.push(
                <Item key={key} to={{pathname: '/course', state: key}}>
                    <Progress>
                        <Picture pictureUrl={courses[key].pictureUrl} />
                    </Progress>
                    <Title>{key}</Title>
                </Item>
            )
        }

        return courseCards;
    };

    return (
        <Carousel>
            {constructCourseCards(courses)}
        </Carousel>
    )
};

export default CourseOverview;