import React from 'react';
import {animated, useTransition} from "react-spring";

export const useClickAway = (callback, active) => {
    // Use active for enabling and disabling the listener
    const ref = React.useRef();
    const [clickAway, setClickAway] = React.useState(false);

    const handleClickAway = React.useCallback((event) => {
        if (!active && active !== undefined) return;

        if (ref.current && !ref.current.contains(event.target) && ref.current !== event.target) {
            setClickAway(true);
            callback();
        } else {
            setClickAway(false);
        }
    }, [callback, active, setClickAway, ref]);

    React.useEffect(() => {
        document.addEventListener('click', handleClickAway);

        return () => {
            document.removeEventListener('click', handleClickAway);
        }
    }, [clickAway, handleClickAway]);

    return ref;
};

export const useMeasure = () => {
    // For x,y only use left and right for IE and Edge Support
    const ref = React.useRef();

    const [measurement, setMeasurement] = React.useState({
        width: 0,
        height: 0,
        x: 0,
        y: 0,
        left: 0,
        right: 0,
        top: 0,
        bottom: 0,
    });

    const measure = () => {
        setMeasurement(ref.current.getBoundingClientRect().toJSON());
    };

    React.useLayoutEffect(() => {
        measure();
    }, [ref]);

    React.useEffect(() => {
        window.addEventListener('resize', measure, {passive: true, capture: false});

        return () => {
            window.removeEventListener('resize', measure);
        }
    });

    return [ref, measurement];
};

export const useUniqueId = (length) => {
    let id = '';

    for(let i = 0; i < length; i++) {
        id += String.fromCharCode(Math.floor(Math.random() * 255));
    }

    const [state] = React.useState(id);

    return state;
};

export const useRipple = ({duration, color, size} = {duration: 500, color: 'rgba(0, 0, 0, 0.5)', size: 10}) => {
    const ref = React.useRef();

    const [state, setState] = React.useState([]);

    React.useEffect(() => {
        ref.current.addEventListener('click', ripple);

        const copyRef = ref;

        return () => {
            copyRef.current.removeEventListener('click', ripple);
        }
    });

    const uniqueId = () => {
        let string = '';

        for(let i = 0; i < 16; i++) {
            string += String.fromCharCode(Math.floor(Math.random() * 255));
        }

        return string;
    }

    const ripple = (event) => {
        const rect = ref.current.getBoundingClientRect();

        setState(state.concat(
            {key: uniqueId(), x: event.clientX - rect.x, y: event.clientY - rect.y}
        ));

        window.setTimeout(removeLast, duration * 2);
    }

    const removeLast = () => {
        setState(state => {
            const [first, ...rest] = state;

            return rest;
        });
    }

    const transition = useTransition(state, item => item.key, {
        from: {transform: 'scale(0)', opacity: 1},
        enter: {transform: `scale(${size})`, opacity: 0},
        leave: {transform: `scale(${size})`, opacity: 0}
    })

    return [
        ref,
        transition.map(({item, props, key}) =>
            <animated.div
                key={item.key}
                style={{
                    ...props,
                    left: item.x,
                    top: item.y,
                    backgroundColor: color,
                    width: 10,
                    height: 10,
                    position: 'absolute',
                    borderRadius: 5
                }}
            />)
    ];
};