import React from 'react';
import styled from 'styled-components';
import {animated, config, useSpring} from "react-spring";
import Icon from "../Icon";
import {useClickAway} from "../Hooks";
import Button from "../Atomic/Button";

const Base = animated(styled.div`
    position: fixed;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.25);
    z-index: 1500;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
`);

const Container = animated(styled.div`
    background-color: white;
    border-radius: 4px;
    overflow: hidden;
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.5);
    max-width: calc(100% - 32px);
`);

const Title = styled.div`
    padding: 16px;
    background-color: ${props => props.theme.primary.main};
    color: ${props => props.theme.primary.color};
    display: flex;
    align-items: space-evenly;
    justify-content: space-between;
`;

const Message = styled.div`
    padding: 16px;
    display: flex;
`;

const ButtonBox = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
`;

const Description = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
`;

const Progress = styled.div`

`;

const Text = styled.div`
    margin-bottom: 8px;
`;

const Modal = ({active, close, title}) => {
    const {backgroundColor, transform, opacity} = useSpring({
        opacity: active ? 1 : 0,
        backgroundColor: `rgba(0, 0, 0, ${active ? 0.5 : 0})`,
        transform: 'scale(' + (active ? 1 : 0) + ')',
        config: config.gentle,
    });

    const ref = useClickAway(close, active);

    return (
        <Base style={{
            backgroundColor: backgroundColor,
            visibility: opacity.interpolate(x => x > 0 ? 'visible' : 'hidden')
        }}>
            <Container ref={ref} style={{
                transform: transform,
                opacity: opacity,
            }}>
                <Title>
                    {title}
                    <Icon onClick={close}>close</Icon>
                </Title>
                <Message>
                    <Description>
                        <Text>
                            In dieser Einheit wird ihnen das Thema "{title}" näher erläutert.
                        </Text>
                        <Progress>
                            Ihr aktueller Fortschritt beträgt 0%.
                        </Progress>
                    </Description>
                    <ButtonBox>
                        <Button>
                            <Icon>
                                play_arrow
                            </Icon>
                        </Button>
                        <Button>
                            <Icon>
                                speed
                            </Icon>
                        </Button>
                    </ButtonBox>
                </Message>
            </Container>
        </Base>
    )
};

export default Modal;