import styled from 'styled-components';

const Avatar = styled.div`
      width: 48px;
      height: 48px;
      border-radius: 50%;
      background-color: ${props => props.theme.secondary.main};
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 12pt;
      color: ${props => props.theme.secondary.color};
`;

export default Avatar;