import React from 'react';
import styled from 'styled-components';
import CardBase from "./CardBase";

const Title = styled.div`
    font-size: 14pt;
    padding: 16px;
`;

const CardOverview = ({title, children,...props}) => {

    return (
        <CardBase>
            <Title>
                {title}
            </Title>
            {children}
        </CardBase>
    )
};

export default CardOverview;