import React, {useEffect} from 'react';
import styled from 'styled-components';
import {useSpring, animated} from "react-spring";

const Base = animated(styled.div`
    overflow: hidden;
    position: relative;
`);

const Shader = animated(styled.div`
    width: calc(100% + 20px);
    margin-left: -20px;
    position: absolute;
    height: 100%;
    transform: translateX(100%);
    background-image: linear-gradient(90deg, #0d47a100 0px,  #0d47a1ff 20px,  #0d47a1ff 100%);
`);

const Fader = ({children, ...props}) => {
    const [state, setState] = React.useState(children);

    useEffect(() => {
        if(children.key !== state.key) transition();
    });

    const transition = () => {
        setSpring({...animationIn, onRest: () => {
            setState(children);
            setSpring(animationOut);
        }});
    };

    const [style, setSpring] = useSpring(() => ({transform: 'translateX(100%)'}));

    const animationIn = {
        from: {transform: 'translateX(100%)'},
        to: {transform: 'translateX(0%)'},
    };

    const animationOut = {
        from: {transform: 'translateX(0%)'},
        to: {transform: 'translateX(100%)'},
    };

    return (
        <Base>
            <Shader style={style} />
            {state}
        </Base>
    )
};

export default Fader;