import React from 'react';
import styled from 'styled-components';
import {useRipple} from "../Hooks";
import {useSpring, animated, config} from "react-spring";

const Base = animated(styled.div`
    position: relative;
    transition: 0.1s ease background-color, 0.1 ease box-shadow;
    display: inline-block;
    padding: 16px;
    margin: 8px;
    border-radius: 4px;
    ${props => props.theme.elevation[4]}
    background-color: ${props => props.theme.secondary.main};
    overflow: hidden;
`);

const Button = ({children}) => {
    const [ref, ripples] = useRipple({duration: 500, color: 'rgba(255, 255, 255, 0.5)', size: 10});

    const [props, setSpring] = useSpring(() => ({config: config.wobbly}));

    const handleClick = () => {
        setSpring({
            from: {transform: 'scale(1.1)'},
            to: async (next,cancel) => {
                await next({transform: 'scale(1.1)'})
                await next({transform: 'scale(1)'})
            }
        })
    };

    return (
        <Base ref={ref} style={props} onClick={handleClick}>
            {ripples}
            {children}
        </Base>
    )
};

export default Button;