import React from 'react';
import styled, {withTheme} from 'styled-components';
import Icon from "./Icon";
import {animated, config, useSpring} from "react-spring";
import {useRipple} from "./Hooks";

const Base = animated(styled.div`
    position: relative;
    overflow: hidden;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 12px;
    color: ${props => props.color.color};
    
    ${props => {
    switch (props.size) {
        case 'small' :
            return 'padding: 8px';
        case 'medium':
            return 'padding: 12px';
        case 'large' :
            return 'padding: 16px';
        default:
            return 'padding: 8px';
    }
}}
`);

const FAB = ({size, icon, onClick = (() => {}), color, theme}) => {
    color = color || {main: theme.secondary.main, highlight: theme.secondary.light, color: theme.secondary.color};

    const [ref, ripples] = useRipple({
        color: 'rgba(255, 255, 255, 0.5)',
        size: 10,
        duration: 500
    });

    const [props, setSpring] = useSpring(() => {
        return {transform: 'scale(1)', backgroundColor: color.main, boxShadow: theme.elevation[5].slice(12, -1), config: config.wobbly}
    });

    const handleClick = () => {
        setSpring({
            from: {transform: 'scale(1)', backgroundColor: color.main, boxShadow: theme.elevation[5].slice(12, -1)},
            to: async(next, cancel) => {
                await next({transform: 'scale(1.25)', backgroundColor: color.highlight, boxShadow: theme.elevation[7].slice(12, -1)})
                await next({transform: 'scale(1)', backgroundColor: color.main, boxShadow: theme.elevation[5].slice(12, -1)})
            }
        })
    };

    return (
        <Base size={size}
              ref={ref}
              style={props}
              color={color}
              onClick={() => {
                  onClick();
                  handleClick();
              }}>
            {ripples}
            <Icon>{icon}</Icon>
        </Base>
    )
};

export default withTheme(FAB);