import React from 'react';
import styled from 'styled-components';
import {useAuth, useIsAuthenticated} from "../Context/AuthProvider";
import {useTransition, animated} from "react-spring";
import Input from "../Atomic/Input";

const Base = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 200px;
    padding: 16px;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    ${props => props.theme.elevation[4]}
`;

const Title = styled.div`
    padding: 8px;
`;

const Form = styled.form`
    display: flex;
    flex-direction: column;
`;

const Login = ({children}) => {
    const [login] = useAuth();

    const [state, setState] = React.useState({
        user: '',
        password: '',
    });

    const auth = useIsAuthenticated();

    const transition = useTransition(auth, null, {
        from: {position: 'absolute', opacity: 0},
        enter: {opacity: 1, position: ''},
        leave: {opacity: 0, position: 'absolute'},
    })

    const renderLogin = () => {
        return (
            <Base>
                <Title>Login</Title>
                <Form onSubmit={(event) => {
                    event.preventDefault();
                    login(state.user, state.password);
                }}>
                    <Input type='text' placeholder={'User'} value={state.user}
                           onChange={(event) => setState({...state, user: event.target.value})} />
                    <Input type='password' placeholder={'Password'} value={state.password}
                           onChange={(event) => setState({...state, password: event.target.value})} />
                    <input type={'submit'} style={{visibility: 'hidden'}}/>
                </Form>
            </Base>
        )
    }

    return transition.map(({item, key, props}) =>
        !item ? <animated.div key={0} style={props}>{renderLogin()}</animated.div>
            : <animated.div key={1} style={props}>{children}</animated.div>
    )
};

export default Login;